<template>
  <div class="pdf-iframe">
    <iframe :src="html" id="pdf_iframe"></iframe>
  </div>
</template>

<script>
import conn from '@/service/conn'
import tool from '@/common/es/tool'
import config from '@/common/config'

import Loading from '../Shimmer/Loading.vue'

export default {
  components: { Loading },
  
  name: 'PdfIframe',
  props: {
    uri: {
      type: String
    }
  },
  data() {
    return {
      show: false,
      html: ''
    }
  },
  async mounted() {
    
    this.html = this.uri
    
    setTimeout(() => { this.$emit('blocking', true) }, 2000)
  },
  methods: {
    locationData(v, html) {
      this.html = html
      console.log('重赋值。。')
      // this.show = v

      setTimeout(() => {
        this.$emit('blocking', true)
      }, 7000)
    },
    printPDF() {
      
      try {

        const _dom = window.parent.document.getElementById('pdf_iframe')
        
        _dom.contentWindow.printPDF()
        
      } catch (err) {
        
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/pdf.sass"
.d-none 
  display: none !important

.pdf-iframe 
  position: fixed
  left: 15px
  top: 100vh
  border-radius: 3px
  visibility: inherit !important
  // z-index: 9999

.pdf-iframe, .pdf-iframe iframe
  width: calc( 100vw - 30px )
  height: 20vh
  border: none !important
  visibility: inherit !important
</style>


